
import headerBack from "../../components/header-back";
import XImg from 'vux/src/components/x-img/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

import VueDB from "../../util/vue-db/vue-db-long.js";
var DB = new VueDB();
export default{
data(){
return {
params:'',
headTitle:'地址管理',
storeHeader:"../../../static/images/classStore/storeHeader.jpg",
storeItem1:"../../../static/images/classStore/storeItem1.jpg",
storeItem2:"../../../static/images/classStore/storeItem2.jpg",
editAddr:"../../../static/images/classStore/editAddr.png",
addrList:[],
}
},
mounted(){
this.initData();
},
methods:{
initData(itemNo){
var telUserNo = DB.getItem("telUserNo").toJson();
this.$http.get("/userInfo/findUserPostAddress?userNo="+telUserNo.userNo).then((res) => {
console.log(res)
if(res.data.data.length){
var resData = res.data.data;
console.log(resData)
resData.map((item,index)=>{
item.defaultAddress = Number(item.defaultAddress);
})
this.addrList = resData;

}
}).catch((err) => {
console.log(err)
})


},
cClassData(itemData,itemNo){
var _this = this;
var classData = itemData;
this.classData.forEach(function(item,index){
if(item.classId == itemNo){
_this.storeList = item.stores;
}
})
},

saveAddr(){
this.$router.go(-1);
},
open(link,param,item){
console.log(item)

if (item) {
console.log("Item")
DB.setItem('editAddr',item);
} else{
console.log("noItem")
}
this.$router.openPage(link,{params:param});
},
toCloseAccount(link,param,item){
item.defaultAddress = item.defaultAddress == 0 ? false : true;
DB.setItem('selectAddr',item);
this.$router.openPage(link,{params:param});
}
},
components: {
Swiper,
SwiperItem,
headerBack,
XImg,
Divider
},
}



